import React, {useEffect} from 'react'
import classes from './App.module.scss'
import Menu from './views/Menu/Menu'
import ChooseSquad from './views/ChooseSquad/ChooseSquad'
import {
  HashRouter as Router,
  Switch,
  Route,
} from 'react-router-dom'
import GameEnd from './views/GameEnd/GameEnd'
import PlayField from './views/PlayField/PlayField'
import Cards from './views/Cards/Cards'
import {useDispatch, useSelector} from 'react-redux'
import {useTranslation} from 'react-i18next'
import {gameActions} from '../store/gameReducer'
import {loadGame} from '../storage/localStorage'

export const ROUTES = {
  MAIN: {
    path: '/'
  },
  CHOOSE_SQUAD: {
    path: '/choose-squad'
  },
  GAME_END: {
    path: '/game-end'
  },
  PLAY_FIELD: {
    path: '/play-field'
  },
  CARDS: {
    path: '/cards'
  }
}

function App() {
  const dispatch = useDispatch()
  const {i18n} = useTranslation()
  const {language} = useSelector(({settings}) => settings)
  const {rounds} = useSelector(({game}) => game)

  useEffect(() => {
    i18n.changeLanguage(language)
  }, [language])

  useEffect(() => {
    const _fetchCards = () => {
      const characters = [
        'Обрин отважный',
        'Северин Стальное Сердце',
        'Ангард Светлый Щит'
      ]

      dispatch(gameActions.setRounds(characters.map((name, i) => ({
        activationTokens: [
          {isActive: true},
          {isActive: true},
          {isActive: true},
          {isActive: true}
        ],
        cards: [{
          id: i,
          name,
          healthPoints: 5,
          move: {applied: false},
          attack: {applied: false},
          defend: {applied: false},
          dodge: {applied: false}
        }],
        gloryPoints: {
          wasted: {
            count: 0
          },
          notWasted: {
            count: 0
          }
        }
      }))))
    }

    if (!rounds.length) {
      _fetchCards()
    }
  }, [rounds, dispatch])

  return (
    <Router>
      <div className={classes.content}>
        <Switch>
          <Route path={ROUTES.CARDS.path} render={() => <Cards/>}/>
          <Route path={ROUTES.CHOOSE_SQUAD.path} render={() => <ChooseSquad/>}/>
          <Route path={ROUTES.GAME_END.path} render={() => <GameEnd/>}/>
          <Route path={ROUTES.PLAY_FIELD.path} render={() => <PlayField/>}/>
          <Route exact path={ROUTES.MAIN.path} render={() => <Menu/>}/>
        </Switch>
      </div>
    </Router>
  )
}

export default App
