import React from "react";
import * as styles from "./FighterItem.module.scss";
import * as classnames from 'classnames'
import {makeImageIconUrl} from '../../../utils/utils'

const FighterItem = ({name, isDead, image, className = "", isFocused, ...restProps}) => {
  return (
    <div
      className={classnames(styles.root, className, {
        [styles.focused]: isFocused,
        [styles.dead]: isDead
      })}
      {...restProps}
    >
      <div className={styles.imageContainer}>
        <img className={styles.imageFighter} src={image} alt={name} />
        <img className={styles.imageIsDead} src={makeImageIconUrl("ic_fighter_is_dead")} alt="Is dead"/>
      </div>
    </div>
  )
};

export default FighterItem;